.lbl {
    font-size: 0.85em;
}

.shopping-title-wrapper {
    margin-left: 32px;
}

.shopping-title {
    font-size: 1.2em;
    font-weight: bold;
}

.num-results {
    font-size: 1.0em;
    margin-top: 4px;
}

.num-results-modal {
    font-size: 1.1em;
    font-weight: bold;
    margin-top: 4px;
    margin-bottom: 8px;
}

.filter-btn {
    --green-btn:  rgb(0, 127, 115);
    background-color: white;
    border-radius: 3px;  
    border: 1px solid rgb(180, 186, 193);
    color: var(--green-btn);  
    cursor: pointer;
    font-size: 0.9em;
    font-weight: bold;
    height: 34px;
    margin: 16px 0;
    width: 80%;
  }
  

.shopping-page-container {
    display: flex;
    margin-top: 15px;
    max-width: 1248px;
    margin: 16px;
}


/*====== Filter Stuff ====================== */
/*  
 * Screen size < 768:  a button that uses a modal to select the shopping filters.
 * Screen size > 768:  a filter sidebar
 *
 *=========================================== */

.filter-box {
    display: none;
}

@media screen and (min-width: 768px ) {

    .filter-btn-wrapper {
        display: none;
    }

    .filter-box {
      display: block;
      width: 248px;
      flex-grow: 0;
      flex-shrink: 0;
    }
  }
  
@media screen and (min-width: 1024px ) {

    .filter-box {
        width: 312px;
    }
}

  
.filter-section {
    padding-left: 15px;
}

.filter-title {
    margin-bottom: 10px;
}

.top2_filters {
    background-color: whitesmoke;
    padding-top: 1px;
    padding-bottom: 10px;
    margin-right: 10px;
    border-radius: 10px;
}

.price-range-wrapper {
    margin-bottom: 5px;
}

.price-range-form {
    padding: 0;
    text-align: left;
}

.price-input {
    width: 60px;
    height: 30px;
}

.price-button {
    background-color: white;
    color: forestgreen;
    border: 1px solid black;
    width: 50px;
    height: 36px;
    font-size: 0.9rem;
    font-weight: bold;
    margin-left: 10px;
}
.price-input-to {
    margin-left: 5px;
    margin-right: 5px;
    font-size: 0.9rem;
}

summary {
    margin-top: 15px;
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 1.2rem;
}

/*====== Product Grid ======*/

.product-grid {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
}

@media screen and (min-width: 768px ) {
    .product-grid {
        grid-template-columns: repeat(3, 33fr);
    }
}

